<template>
  <div style="padding: 1.143rem 1.286rem;">
    <van-cell is-link title="头像">
      <van-image
        round
        slot
        width="1.929rem"
        height="1.929rem"
        :src="perinfor.head_img"
      />
    </van-cell>
    <van-cell is-link title="昵称">
      <span>{{perinfor.name}}</span>
    </van-cell>
    <van-cell title="手机号">{{perinfor.tel}}</van-cell>
    <van-cell title="健康证明">
      <img src="../../assets/images/personal/prove.png" alt style="width:25px;" />
    </van-cell>
  </div>
</template>

<script>
import { userInfo } from "@/api/api";
export default {
  name: "personal-info",
  data() {
    return {
      perinfor: {},
      type: 0
    };
  },
  created() {
    this.type = localStorage.getItem("type");
    let data = {
      move_id: localStorage.getItem("id"),
      type: localStorage.getItem("type")
    };
    userInfo(data).then(res => {
      this.perinfor = res.data.data;
    });
  }
};
</script>

<style scoped lang="less">
/deep/ .van-cell {
  padding: 0;
  height: 3.214rem;
  display: flex;
  align-items: center;
  color: #989898;
  font-size: 0.857rem;
  border-bottom: 1px solid #f2f2f2;
}

/deep/ .van-cell__title {
  padding-left: 1.286rem;
  margin-top: 0.214rem;
  font-size: 0.786rem;
  color: #282828;
}

